<template>
  <div class="content">

    <!--下啦刷新-->
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <template #pulling><refreshLoading text="下拉刷新"/></template>
      <template #loosing><refreshLoading text="下拉刷新"/></template>
      <template #loading><refreshLoading text="加载中"/></template>
      <template #success><refreshLoading text="加载完成"/></template>
      <!--上啦加载-->
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多啦~"
        @load="onLoad"
        van-clearfix
      >
        <van-row>
          <van-col @click="viewDetail(item.id)" class="line" v-for="(item, index) in resList" :key="index">
            <img :src="item.imageUrl | imageToHttps" alt="" />
            <div class="title">
              {{item.name}}
            </div>
          </van-col>
        </van-row>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import refreshLoading from '@/components/refreshLoading'

/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';

/**
 * vant框架
 */
import Vue from 'vue';
import { List, Toast, PullRefresh, Dialog } from 'vant';
// 全局注册
Vue.use({ PullRefresh, Toast, List, Dialog });

/*
 * 成套解决方案列表
*/
export default {
  name: 'itSoluteList',
  components: {
    refreshLoading,
  },
  data() {
    return {
      /**
       * {pageIndex} 页码
       * {pageSize} 每页条数，大小
       * {resList} 接口返回数据列表
       * {resData} 接口返回数据
       * {isLoading} 下啦刷新loading
       * {loading} 上啦加载更多的loading
       * {finished} 上啦加载完成
       * {channel} 渠道1:app 2:pc
       * {productId} 商品id
       */
      resList: [],
      resData: null,
      error: false,
      loading: false,
      finished: false,
      pageIndex: 0,
      pageSize: 5,
      isLoading: false,
      channel: 1,
      productId: '',
    }
  },
  created() {
    const context = this;
    const _query = context.$route.query;
    context.productId = _query.productId;
  },
  mounted() {
  },
  methods: {
    /**
     * @description 下拉刷新,下啦刷新同时清空现有数据，页码初始化0
     */
    onRefresh() {
      const context = this;
      context.pageIndex = 0;
      context.loading = true;
      context.finished = false;
      /**
       * @description 页面上拉触底事件的处理函数，加载更多
      */
      context.onLoad();
    },

    /**
     * 页面上拉触底事件的处理函数，加载更多
     */
    onLoad() {
      const context = this;
      context.pageIndex = context.pageIndex + 1;
      /**
       * @description 个人中心 我的优惠券列表
      */
      context.getList();
    },

    /**
     * @description  个人中心 我的优惠券列表
     * @param {pageSize} 每页条数大小
     * @param {pageIndex} 第几页
     * @param {channel} 渠道1:app 2:pc
     * @param {productId} 商品id
     * @param {resList} 接口返回数据列表
     * 返回接口请求数据
    */
    getList () {
      const context = this;
      const data = {
        pageSize: context.pageSize,
        pageIndex: context.pageIndex,
        channel: context.channel,
        productId: context.productId
      }
      personalApi.getListByProductId(data).then(res => {
        const _resData = res.data;
        // 清理list
        if (context.pageIndex === 1) {
          context.resList = [];
        }
        if (_resData.success) {
          context.resData = _resData.data;
          context.resList = context.resList.concat(_resData.data.details);
          // 加载状态结束
          context.loading = false;
          // 下啦刷新完成
          if (context.isLoading) {
            // Toast.success('刷新成功');
            context.isLoading = false;
          }
          // 数据全部加载完成
          if (_resData.data.details.length < context.pageSize) {
            context.finished = true;
          }
        } else {
          Toast.fail(_resData.message);
        }
      })
    },

    /**
     * @description 跳转到查看详情页面
     * @param {}
    */
    viewDetail (id) {
      const context = this;
      context.$router.push({
        path: `/itSoluteDetail?id=${id}`,
      });
    },

  },
}
</script>

