.content {
  padding: 0.16rem 0 0 0;

  .van-row {
    background: #ffffff;

    /**
    * list列表数据
    */
    .line {
      width: 7.50rem;
      height: 3.5rem;
      padding: 0 0.32rem;
      background: rgba(255,255,255,1);
      position: relative;
      margin: 0.24rem 0 0 0;

      img {
        display: block;
        width: 6.88rem;
        height: 3.5rem;
      }

      .title {
        position: absolute;
        bottom: 0;
        width: 6.56rem;
        height: 0.56rem;
        background: rgba(0,0,0,.4);
        font-size: 0.28rem;
        font-weight: 500;
        color: #ffffff;
        line-height: 0.56rem;
        padding: 0 0.16rem;
      }

    }

    .line:last-of-type {
      margin-bottom: 0.24rem;
    }

  }

}

</style>
